import { css, styled } from '../../utils/styled';
import { color, excludeProps, radius, spacing, zIndex } from '../../utils/theme';
import { PopoverArrow as BaseArrow, PopoverContent as BaseContent } from '../Popover';

const side = '14px';

const centerPosition = css`
	position: absolute; // Set as "absolute" so svg is not affecting line height.
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
`;

export const HintButton = styled.span`
	cursor: pointer;
	display: inline-block;
	vertical-align: baseline;
	width: ${side};
	min-height: ${side};
	border-radius: 50%;
	position: relative; // Necessary so "icon" can be positioned in the center
	line-height: inherit;

	&::before {
		content: '\\a0'; // This space is necessary to hold line-height, which is necessary so Hint is visually aligned with the text
	}
`;

export const IconWrapper = styled.span`
	display: inline-flex;
	fill: ${color('interactive.content.idle')};
	font-size: 16px;
	${centerPosition}
`;

export const Content = styled(BaseContent).withConfig({
	shouldForwardProp: excludeProps(['width']),
})`
	font-size: 14px;
	line-height: 20px;
	background-color: ${color('background.primary')};
	color: ${color('content.secondary')};
	border-radius: ${radius('md')};
	padding: ${spacing(3, 4)};
	border-color: ${color('border.primary')};
	z-index: ${zIndex('tooltip')};
	width: ${({ width }: { width: string }) => width};
`;

export const Arrow = styled(BaseArrow)`
	&:before {
		background-color: ${color('background.primary')};
		border-color: ${color('border.primary')};
	}
`;
